    #header .header__cover_image a {
      padding: 10px; }
      #header .header__cover_image a img {
        -webkit-transition: .1s linear;
        transition: .1s linear;
        max-height: 100vh;
        height: 100%; }
  #header .nav_bar {
    display: flex;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    justify-content: flex-end;
    z-index: 2;
    height: 50px; }
    #header .nav_bar nav {
      display: flex;
      align-items: center;
      height: 100%; }
      #header .nav_bar nav .nav_bar__item {
