.title {
  background: #27ae61;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(-45deg, #dcdce1, #e6eae9);
  background: #d9bbf7;
  font-size: 24px;
  font-family: 'poiret one', -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Helvetica", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans";
  letter-spacing: 4px; }

.folio {
  display: flex;
  justify-content: center; }
  .folio .folio__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 80%;
    border-radius: 10px;
    background: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    overflow: hidden; }
    .folio .folio__items a {
      height: 200px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      -webkit-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out;
      overflow: hidden;
      position: relative; }
      .folio .folio__items a .cover {
        /* background-color: rgba(1, 1, 1, 0.75); */
        background: -webkit-gradient(linear, left bottom, left top, from(rgba(1, 1, 1, 0.85)), to(rgba(1, 1, 1, 0.45)));
        background: linear-gradient(0deg, rgba(1, 1, 1, 0.85), rgba(1, 1, 1, 0.45));
        -webkit-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0px;
        opacity: 0; }
      .folio .folio__items a .project {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat; }
        .folio .folio__items a .project .project__title {
          -webkit-transition: 0.3s ease-in-out;
          transition: 0.3s ease-in-out;
          margin: 0;
          text-align: center;
          width: 100%;
          color: #fff;
          background: rgba(1, 1, 1, 0.6);
          font-family: 'roboto thin', Roboto, 'open sans', 'poiret one', helvetica, arial;
          font-size: 24px;
          position: relative;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
          margin-top: calc(200px/2);
          text-decoration: none; }
        .folio .folio__items a .project .project__description {
          -webkit-transition: 0.3s ease-in-out;
          transition: 0.3s ease-in-out;
          font-size: 14px;
          font-family: helvetica, arial, sans-serif;
          position: relative;
          margin-top: 100%;
          text-decoration: none; }
      .folio .folio__items a .cover {
        opacity: 0;
        background: -webkit-gradient(linear, left bottom, left top, from(rgba(1, 1, 1, 0.85)), to(rgba(1, 1, 1, 0.45)));
        background: linear-gradient(0deg, rgba(1, 1, 1, 0.85), rgba(1, 1, 1, 0.45));
        -webkit-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0; }
      .folio .folio__items a:hover .project__title {
        -webkit-transform: none;
                transform: none;
        background: none;
        margin-top: 10px; }
      .folio .folio__items a:hover .project__description {
        color: #fff;
        margin-top: 10px; }
      .folio .folio__items a:hover .cover {
        opacity: 1; }

#profile {
  display: flex;
  justify-content: center;
  position: relative; }
  #profile .profile--wrapper {
    width: 75%;
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center; }

.profile__title {
  display: flex;
  align-items: center;
  margin: 30px 0;
  width: 100%; }
  .profile__title .profile__title--header {
    flex: 3 1;
    padding: 0 20px;
    text-align: left; }
    .profile__title .profile__title--header h2 {
      font-size: 30px;
      font-weight: 400;
      font-family: helvetica, 'open sans', roboto, 'poiret one'; }
      .profile__title .profile__title--header h2 .blink {
        -webkit-animation: blink 1s infinite;
                animation: blink 1s infinite; }
  .profile__title .profile__title--image {
    flex: 1 1;
    position: relative;
    padding: 0 20px;
    display: flex;
    align-items: center; }
    .profile__title .profile__title--image img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      box-shadow: 10px 10px 30px rgba(1, 1, 1, 0.2); }

.profile__bio {
  text-align: left;
  width: 100%; }
  .profile__bio .profile__bio--text {
    background: #ecf0f1;
    padding: 30px;
    border-radius: 20px;
    margin: 40px 0;
    font-size: 20px; }

.profile__cv {
  margin: 20px 0; }

.profile__cta {
  margin: 20px 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .profile__cta .profile__cta__button {
    background: rgba(0, 0, 0, 0);
    border: 2px solid #2dcc70;
    padding: 16px 20px;
    border-radius: 20px;
    -webkit-transition: .2s linear;
    transition: .2s linear; }
    .profile__cta .profile__cta__button:hover {
      color: #fff;
      box-shadow: 0 0 30px rgba(39, 174, 97, 0.3);
      background-color: #2dcc70; }
    .profile__cta .profile__cta__button:active {
      background-color: #27ae61; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  49% {
    opacity: 1; }
  50% {
    opacity: 0; }
  99% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  49% {
    opacity: 1; }
  50% {
    opacity: 0; }
  99% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@media only screen and (min-width: 1200px) {
  .profile__bio {
    text-align: left;
    width: 70%; } }

